"use client"

import type React from "react"

import { Navigation } from "@/components/navigation"
import { Footer } from "@/components/footer"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { CheckCircle2 } from "lucide-react"
import { useState } from "react"

export default function AssessmentPage() {
  const [submitted, setSubmitted] = useState(false)
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    age: "",
    primaryGoal: "",
    sleepQuality: "",
    energyLevel: "",
    healthConcerns: "",
    program: "",
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    setSubmitted(true)
    // In production, this would send to a backend API
  }

  if (submitted) {
    return (
      <main className="min-h-screen">
        <Navigation />
        <section className="pt-32 pb-24 px-6 lg:px-8">
          <div className="container mx-auto max-w-2xl text-center">
            <div className="flex justify-center mb-6">
              <div className="w-16 h-16 rounded-full bg-primary/10 flex items-center justify-center">
                <CheckCircle2 className="w-8 h-8 text-primary" />
              </div>
            </div>
            <h1 className="font-serif text-4xl sm:text-5xl text-foreground mb-6">Assessment Submitted</h1>
            <p className="text-lg text-muted-foreground mb-8 leading-relaxed">
              Thank you for your interest in Mendiv. Our team will review your assessment and reach out within 24-48
              hours to discuss the next steps.
            </p>
            <p className="text-muted-foreground mb-8">
              In the meantime, check your email for a confirmation and additional information about our programs.
            </p>
            <Button size="lg" onClick={() => (window.location.href = "/")}>
              Return to Home
            </Button>
          </div>
        </section>
        <Footer />
      </main>
    )
  }

  return (
    <main className="min-h-screen">
      <Navigation />

      {/* Hero Section */}
      <section className="relative pt-32 pb-24 px-6 lg:px-8 overflow-hidden">
        {/* Background Image */}
        <div
          className="absolute inset-0 -z-20 bg-cover bg-center bg-no-repeat"
          style={{
            backgroundImage: "url('/hero2.png')",
          }}
        />
        
        {/* Light Overlay for readability */}
        <div className="absolute inset-0 -z-10 bg-white/80 backdrop-blur-sm" />

        <div className="container max-w-5xl relative z-0 ml-0 lg:ml-8 xl:ml-16">
          <div className="text-left">
            {/* Badge */}
            <div className="inline-flex items-center px-3 py-1.5 rounded-md bg-primary/10 text-foreground text-sm font-medium mb-6">
              Health Assessment
            </div>

            {/* Headline with gradient */}
            <h1 className="font-serif text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-foreground mb-6 leading-tight">
              Start your{" "}
              <span className="bg-gradient-to-r from-green-600 via-green-500 to-green-700 bg-clip-text text-transparent">
                transformation
              </span>
            </h1>

            {/* Description */}
            <p className="text-lg sm:text-xl text-muted-foreground leading-relaxed max-w-2xl mb-8">
              Tell us about yourself so we can recommend the best program for your goals and create a personalized optimization plan.
              <br />
              Begin your journey to optimal health and longevity.
            </p>

            {/* CTA Buttons - Scroll to form */}
            <div className="flex flex-col sm:flex-row items-start gap-4">
              <Button
                size="lg"
                className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800 text-white px-8 h-12 text-base rounded-lg"
                onClick={() => {
                  document.getElementById("assessment-form")?.scrollIntoView({ behavior: "smooth" })
                }}
              >
                Complete Assessment
              </Button>
              <Link href="/contact">
                <Button
                  size="lg"
                  variant="outline"
                  className="bg-white border border-gray-300 text-gray-700 hover:bg-gray-50 px-8 h-12 text-base rounded-lg"
                >
                  Book Consultation
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </section>

      {/* Form Section */}
      <section id="assessment-form" className="py-12 px-6 lg:px-8">
        <div className="container mx-auto max-w-3xl">
          <Card className="border-2">
            <CardHeader>
              <CardTitle className="font-serif text-2xl">Personal Information</CardTitle>
              <CardDescription>We'll use this information to create your personalized assessment</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                  <div className="space-y-2">
                    <Label htmlFor="firstName">First Name *</Label>
                    <Input
                      id="firstName"
                      required
                      value={formData.firstName}
                      onChange={(e) => setFormData({ ...formData, firstName: e.target.value })}
                      placeholder="John"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="lastName">Last Name *</Label>
                    <Input
                      id="lastName"
                      required
                      value={formData.lastName}
                      onChange={(e) => setFormData({ ...formData, lastName: e.target.value })}
                      placeholder="Smith"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                  <div className="space-y-2">
                    <Label htmlFor="email">Email Address *</Label>
                    <Input
                      id="email"
                      type="email"
                      required
                      value={formData.email}
                      onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                      placeholder="john@example.com"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="phone">Phone Number</Label>
                    <Input
                      id="phone"
                      type="tel"
                      value={formData.phone}
                      onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                      placeholder="+1 (555) 000-0000"
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="age">Age Range *</Label>
                  <select
                    id="age"
                    required
                    value={formData.age}
                    onChange={(e) => setFormData({ ...formData, age: e.target.value })}
                    className="w-full px-3 py-2 rounded-md border border-input bg-background focus:outline-none focus:ring-2 focus:ring-ring"
                  >
                    <option value="">Select age range</option>
                    <option value="25-34">25-34</option>
                    <option value="35-44">35-44</option>
                    <option value="45-54">45-54</option>
                    <option value="55-64">55-64</option>
                    <option value="65+">65+</option>
                  </select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="program">Program Interest *</Label>
                  <select
                    id="program"
                    required
                    value={formData.program}
                    onChange={(e) => setFormData({ ...formData, program: e.target.value })}
                    className="w-full px-3 py-2 rounded-md border border-input bg-background focus:outline-none focus:ring-2 focus:ring-ring"
                  >
                    <option value="">Select a program</option>
                    <option value="bioalign">BioAlign™ - Full Life Optimization</option>
                    <option value="neurosleep">NeuroSleep™ - Precision Sleep Reset</option>
                    <option value="unsure">Not sure yet</option>
                  </select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="primaryGoal">Primary Health Goal *</Label>
                  <Textarea
                    id="primaryGoal"
                    required
                    value={formData.primaryGoal}
                    onChange={(e) => setFormData({ ...formData, primaryGoal: e.target.value })}
                    placeholder="What's your main objective? (e.g., improve sleep, increase energy, optimize longevity, enhance performance)"
                    rows={3}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="sleepQuality">Sleep Quality (1-10) *</Label>
                  <Input
                    id="sleepQuality"
                    type="number"
                    min="1"
                    max="10"
                    required
                    value={formData.sleepQuality}
                    onChange={(e) => setFormData({ ...formData, sleepQuality: e.target.value })}
                    placeholder="Rate your current sleep quality"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="energyLevel">Energy Level (1-10) *</Label>
                  <Input
                    id="energyLevel"
                    type="number"
                    min="1"
                    max="10"
                    required
                    value={formData.energyLevel}
                    onChange={(e) => setFormData({ ...formData, energyLevel: e.target.value })}
                    placeholder="Rate your current energy level"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="healthConcerns">Current Health Concerns</Label>
                  <Textarea
                    id="healthConcerns"
                    value={formData.healthConcerns}
                    onChange={(e) => setFormData({ ...formData, healthConcerns: e.target.value })}
                    placeholder="Any specific health issues, medications, or concerns we should know about?"
                    rows={4}
                  />
                </div>

                <div className="pt-6">
                  <Button type="submit" size="lg" className="w-full">
                    Submit Assessment
                  </Button>
                </div>

                <p className="text-xs text-muted-foreground text-center">
                  By submitting this form, you agree to our privacy policy and consent to be contacted by our team.
                </p>
              </form>
            </CardContent>
          </Card>
        </div>
      </section>

      <Footer />
    </main>
  )
}
